.article-container .banner {
  width: 100%;
  height: 440px;
  margin: 0 auto;
  background: skyblue url(https://public.fuyoukache.com/fbfe0a93-6381-47ab-95d8-cd9225801880---banner1.jpg) no-repeat;
  background-size: cover;
  background-position: left center; }

.article-container .content-wrapper {
  width: 800px;
  margin: 0 auto; }
  .article-container .content-wrapper h1 {
    height: 45px;
    line-height: 45px;
    margin: 20px 0;
    font-size: 32px;
    color: #333; }
  .article-container .content-wrapper .stamp {
    overflow: hidden;
    font-size: 14px;
    line-height: 20px; }
    .article-container .content-wrapper .stamp .publish-date {
      color: #909090;
      float: left;
      vertical-align: middle; }
    .article-container .content-wrapper .stamp .view-num {
      margin: 0 18px;
      float: left;
      vertical-align: middle; }
    .article-container .content-wrapper .stamp .article-tags {
      vertical-align: middle; }
      .article-container .content-wrapper .stamp .article-tags b {
        display: block;
        font-weight: 400;
        float: left;
        padding: 0px 4px;
        font-size: 0.7rem;
        color: #fff;
        border: none;
        margin-right: 8px;
        background: rgba(0, 0, 0, 0.4);
        border-radius: 2px; }



