.backTop {
  background: url("http://i1.fuimg.com/690255/765779aa0d422643.png") no-repeat 0 0;
  width: 70px;
  height: 900px;
  position: fixed;
  top: -900px;
  right: 25px;
  -webkit-transition: 600ms all cubic-bezier(0.25, 0.1, 0.3, 1.5);
  transition: 600ms all cubic-bezier(0.25, 0.1, 0.3, 1.5);
  -webkit-transform: transLateY(-1000px);
          transform: transLateY(-1000px);
  -webkit-animation: back 2s linear infinite;
          animation: back 2s linear infinite;
  outline: none;
  z-index: 5; }
  .backTop:hover {
    cursor: pointer; }

@-webkit-keyframes back {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes back {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }



